:root {
  --mainFont: "Roboto";
  --firaSans: "Fira Sans Extra Condensed", sans-serif;
  --robotoCond: "Roboto Condensed", sans-serif;
  --inter: "Inter", sans-serif;
  --bodyBg: #F0F2F4;
  --mainMargin: 24px;
  --borderLine: 1px solid rgba(91, 91, 91, 0.2);
  --btnColor: #0083F1;
  --containerWidth: 1028px;
}

.filter, .shop__item, .catalog__regulate, .cart__notify, .cart__form-bottom, .cart__form-top, .cart__item, .news__article, .goods__product, .profile__about, .profile__setup-info, .profile__info {
  padding: 24px;
  background: #fff;
  border-radius: 10px;
}

.profile__setup-top, .profile__table-item {
  border-bottom: var(--borderLine);
}

.svg-svg-plus, .svg-svg-minus, .svg-svg-loop, .svg-svg-close, .svg-svg-burger, .svg-svg-basket, .svg-svg-arrow-down, .svg-svg-arrow {
