}

.svg-svg-plus-dims {
  width: 12px;
  height: 13px;
}

.image-by-height {
  width: auto;
}

.image-by-height:before {
  content: none;
}

.image-by-height > svg {
  height: inherit;
}

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #FFF;
  font-family: var(--mainFont), "Helvetica", Arial, sans-serif;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--bodyBg);
  font-size: 16px;
  line-height: 118%;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.wrapper {
  width: 100%;
  max-width: 100%;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.container {
  width: 100%;
  max-width: var(--containerWidth);
  padding: 0 var(--mainMargin);
  margin: 0 auto;
}

.title {
  font-family: var(--firaSans);
  font-weight: 400;
  font-size: 24px;
  line-height: 120%;
}

.main-title {
  font-family: var(--robotoCond);
  font-weight: 400;
  font-size: 24px;
  line-height: 120%;
}

.logo {
  display: inline-block;
}
.logo__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  font-family: var(--robotoCond);
}

.btn {
  display: inline-block;
  padding: 5px 12px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  cursor: pointer;
  background: #0083F1;
  color: #fff;
  font-family: var(--robotoCond);
  font-size: 12px;
  line-height: 120%;
  font-weight: 400;
  text-align: center;
}
.btn--empty {
  font-family: var(--firaSans);
  background: #fff;
  color: #000;
  border: 1px solid rgba(0, 0, 0, 0.5);
}
.btn--catalog {
  padding: 14px 24px;
  font-family: var(--robotoCond);
  background: #fff;
  color: #000;
  border: 1px solid rgba(0, 0, 0, 0.5);
  font-size: 16px;
  font-weight: 700;
}
.btn--cart {
  width: 100%;
  padding: 11px 24px;
  font-size: 18px;
  text-align: left;
  border: 1px solid rgba(0, 0, 0, 0.5);
}
.btn--shop {
  padding: 14px 24px;
  background: #fff;
  color: #000;
  border: 1px solid rgba(0, 0, 0, 0.5);
  font-weight: 700;
  font-size: 16px;
}

.field {
  width: auto;
  padding: 5px 8px;
  border-radius: 10px;
  font-size: 12px;
  line-height: 120%;
  font-weight: 400;
  font-family: var(--firaSans);
  background: #fff;
  color: #000;
  border: 1px solid rgba(0, 0, 0, 0.5);
  text-align: center;
  box-sizing: content-box;
}
.field::placeholder {
  text-align: center;
  color: #000;
}
.field:focus::placeholder {
  color: transparent;
}

.choose__item {
  margin-bottom: var(--mainMargin);
}
.choose__item:last-child {
  margin-bottom: 0;
}
.choose__label {
  padding: 12px;
  display: flex;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  cursor: pointer;
}
.choose__radio {
  width: 15px;
  min-width: 15px;
  height: 15px;
  margin-right: 24px;
  border-radius: 50%;
  background: #C4C4C4;
  position: relative;
}
.choose__radio::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background: #0083F1;
  opacity: 0;
  transition: opacity 0.2s ease;
}
.choose__info {
  flex-grow: 1;
  font-family: var(--robotoCond);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.choose__title {
  font-size: 16px;
}
.choose__desc {
  font-size: 10px;
  line-height: 100%;
  color: #858784;
}
.choose__price {
  font-family: var(--firaSans);
  font-size: 12px;
  padding-left: 18px;
}
.choose input {
  display: none;
}
.choose input:checked ~ .choose__radio::after {
  opacity: 1;
}

.selector {
  padding: 5px 26px;
  position: relative;
  font-family: var(--firaSans);
  font-weight: 400;
  font-size: 12px;
  line-height: 120%;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  white-space: nowrap;
  cursor: pointer;
}
.selector__text {
  position: relative;
}
.selector__delete {
  display: none;
  position: absolute;
  right: -14px;
  top: 50%;
  transform: translateY(-50%);
}
.selector__delete img {
  display: block;
}
.selector--active {
  color: #fff;
  background: #0083F1;
  border-color: #0083F1;
}
.selector--active .selector__delete {
  display: block;
}
.selector--disabled {
  color: #C9C9C9;
}

@media screen and (min-width: 500px) {
  .btn--cart {
    text-align: center;
  }
}
@media screen and (min-width: 1280px) {
  .main-title {
    font-size: 28px;
  }

  .choose__price {
    padding-left: 12px;
  }
}
.main {
  flex-grow: 1;
}

.header {
  min-height: 60px;
  padding: 10px 10px;
  background: #fff;
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
.header__burger {
  width: 34px;
  height: 34px;
  margin-top: 2px;
}
.header__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.header__right {
  display: flex;
  align-items: center;
}
.header__right svg {
  max-width: 32px;
  max-height: 32px;
}
.header__search-btn {
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__search-btn svg {
  width: 100%;
  height: 100%;
}
.header__cart {
  display: block;
  margin-left: 6px;
  margin-top: 2px;
  width: 32px;
  height: 34px;
  position: relative;
}
.header__cart svg {
  width: 31px;
  height: 37px;
}
.header__cart-num {
  min-width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #FF0000;
  position: absolute;
  right: -2px;
  top: -2px;
  font-size: 9px;
  line-height: 15px;
  font-weight: bold;
  font-family: var(--inter);
  text-align: center;
  color: #fff;
}

.breadcrumbs {
  font-weight: 400;
  font-size: 14px;
  line-height: 120%;
  font-family: var(--firaSans);
}
.breadcrumbs__list {
  max-width: var(--containerWidth);
  margin: 0 auto;
  padding: var(--mainMargin);
  padding-right: 0;
  display: flex;
  white-space: nowrap;
  overflow-x: auto;
}
.breadcrumbs__item:not(:last-child) {
  margin-right: 7px;
  padding-right: 12px;
  position: relative;
}
.breadcrumbs__item:not(:last-child)::after {
  content: ">";
  position: absolute;
  right: 0;
  top: 0;
  color: #858784;
}
.breadcrumbs__link--active {
  color: #858784;
}

.profile {
  padding-bottom: 10px;
  margin-bottom: var(--mainMargin);
}
.profile__top {
  margin-bottom: var(--mainMargin);
}
.profile__info {
  margin-bottom: var(--mainMargin);
}
.profile__name {
  font-weight: 400;
  font-size: 30px;
  line-height: 120%;
  font-family: var(--firaSans);
}
.profile__ava {
  width: 190px;
  height: 190px;
  margin: 0 auto;
  margin-bottom: var(--mainMargin);
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.profile__ava img {
  display: block;
}
.profile .social {
  margin-bottom: 10px;
}
.profile__table {
  width: 100%;
  font-family: var(--firaSans);
  font-size: 16px;
  line-height: 120%;
  color: #5B5B5B;
}
.profile__table-item {
  padding: 10px 0;
  display: grid;
  grid-template-columns: 1fr 3fr;
}
.profile__table .profile__table-title {
  width: auto;
  min-width: 80px;
  padding-right: 10px;
  color: #000;
  font-weight: 500;
}
.profile__setup {
  margin-bottom: var(--mainMargin);
}
.profile__setup-main {
  width: 294px;
  margin: 0 auto;
}
.profile__setup-heading {
  margin-bottom: var(--mainMargin);
  text-align: center;
}
.profile__setup-top {
  margin-bottom: var(--mainMargin);
  padding: 5px 0;
}
.profile__setup-title {
  text-align: center;
}
.profile__setup-item {
  margin-bottom: 10px;
  border-radius: 10px;
  overflow: hidden;
  display: none;
}
.profile__setup-item.profile__setup-item--active {
  display: block;
}
.profile__setup-item img {
  display: block;
}
.profile__setup-bottom {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.profile__setup-model {
  margin-bottom: 10px;
}
.profile__setup-read {
  color: var(--btnColor);
  font-family: var(--firaSans);
}
.profile__about .title {
  text-align: left;
}
.profile__text {
  font-weight: 300;
}
.profile__text p {
  margin-bottom: 21px;
}
.profile__text p:last-child {
  margin-bottom: 0;
}

@media screen and (min-width: 768px) {
  .profile__setup-main {
    width: 612px;
  }
  .profile__setup-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 24px;
  }
  .profile__setup-item:nth-child(2) {
    display: block;
  }
}
@media screen and (min-width: 1280px) {
  .profile {
